import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { AfrichomeDataProvider } from 'src/providers/africhome-data/africhome-data.provider';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  /**
   * Liste des pays d'Afrique
   */
  public countries: string[];

  constructor(private navCtrl: NavController, private dataProvider: AfrichomeDataProvider) {}

  ngOnInit() {
    this.countries = this.dataProvider.getCountries();
  }

  /**
   * Permet d'être redirigé sur la page de la fonctionnalité sélectionnée
   * @param page : string
   */
  public goToPage(page: string) {
    this.navCtrl.navigateForward('/' + page);
  }

}
